import React from "react";
import { useHistory } from "react-router-dom";
import "./Disclaimer.css";

function Disclaimer() {
  const history = useHistory();
  return (
    <div className="Disclaimer">
      <div className="disclaimer-zone">
        <div className="disclaimer-pic-1"></div>
        <div className="disclaimer-txt">
          <span id="disclaimer-txt-1"> </span>
          <span id="disclaimer-txt-2">
            <br />
            Not affiliated with , associated with , or claiming to be <br />
            Everything is made for roleplay purpose only , nothing real
          </span>
        </div>
        <div className="disclaimer-button-zone">
          <button
            className="disclaimer-button"
            onClick={() => {
              localStorage.setItem("hasVisitedLuvlyFancy", "true");
              history.push("/home");
            }}
          >
             <span>HOME</span>
          </button>
        </div>
      </div>
    </div>
  );
}

export default Disclaimer;
